<template>
    <div class="content">
      <div class="top">
        <div class="sign">
          <div>忘记密码</div>
        </div>
      </div>
      <div class="info">
        <div class="box">
          <!-- <img src="//cdn09.ehaier.com/shunguang/H5/www/img/ic_phone.png" alt=""> -->
          <input type="tel" placeholder="请输入手机号" class="info-mm" v-model="mobileAccount" maxlength=13 required @keyup="keyup">
          <img class="editable-clear-x" @click="clear" :src='`${$cdnImageUrl}/login/clear.png`'>
        </div>
        <div class="box" v-show="captchaShow">
          <!-- <img src="//cdn09.ehaier.com/shunguang/H5/www/img/ic_message.png" alt=""> -->
          <input type="text" placeholder="请输入图形验证码" class="info-mm" v-model="captcha" @blur="verifyCount" maxlength=6>
          <img class="verify-two" :src="src" alt="" @click="getPhoto">
        </div>
        <div class="box">
          <!-- <img src="//cdn09.ehaier.com/shunguang/H5/www/img/ic_message.png" alt=""> -->
          <input type="tel" placeholder="请输入验证码" class="info-mm" v-model="messageMM" @blur="safetyCount" maxlength=6>
          <div class="verify" v-show="!messageMMType" :style="{color:('#ccc')}">{{title}}</div>
          <div class="verify" v-show="messageMMType" @click="find" :style="{color:(allow?'#2283E2':'#333333')}">{{title}}</div>
        </div>
        <div class="box">
          <!-- <img src="//cdn09.ehaier.com/shunguang/H5/www/img/ic_locker.png" alt=""> -->
          <input type="password" placeholder="请输入新密码" class="info-mm" v-model="password" v-show="passwordType" @blur="detection">
          <input type="text" placeholder="请输入新密码" class="info-mm" v-model="password" v-show="!passwordType">
          <img :src="passwordType?`${$cdnImageUrl}/login/unvisible.png`:`${$cdnImageUrl}/login/visible.png`" alt="" class="show" @click="toggle">
        </div>
        <div class="hint">
          <img :src="`${$cdnImageUrl}/login/pwd.png`" alt="">
          密码为数字、字母(区分大小写)、特殊符号中的两种组合，长度为6位-20位
        </div>
        <div class="btn" @click="changeMm">提交</div>
      </div>
    </div>
</template>
<script>
import personalApi from '@/http/service/login.js';
export default {
  data() {
    return {
      mobileAccount: '', // 手机号
      password: '', // 密码
      passwordType: true,
      show: true,
      title: '获取验证码',
      allow: true,
      captcha: '', // 图片验证码
      messageMM: '', // 短信验证码
      // src: 'https://m.ehaier.com/v3/platform/web/member/register/getImgCaptcha.html?rnd=' + Math.random() + '&flag=' + localStorage.getItem('sessionValue'),
      src: '',
      captchaShow: false,
      reg: /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$).{6,20}$/,
      phoneReg: /^1[3456789]\d{9}$/,
      messageMMType: false, // 控制短信验证码显隐
    }
  },
  created() {
    this.$util.smMsgSdk()
    // this.safety()
  },
  watch: {
    // mobileAccount (newValue, oldValue) {
    //   if (newValue.length > oldValue.length) {
    //     this.mobileAccount = newValue.replace(/\s/g, '').replace(/(\d{3})(\d{0,4})(\d{0,4})/, '$1 $2 $3')
    //   } else {
    //     this.mobileAccount = this.mobileAccount.trim()
    //   }
    //   if (newValue.length === 13) {
    //     this.messageMMType = true
    //   } else {
    //     this.messageMMType = false
    //   }
    // },
  },
  methods: {
    /**
   * @description  华为输入法光标错位替代
   */
    keyup() {
      let phone = this.mobileAccount
      phone = phone.replace(/[^0-9]/g, '')
      // 分组成344，并且加上' '
      var temp = []
      temp[0] = phone.slice(0, 3)
      temp[1] = phone.slice(3, 7)
      temp[2] = phone.slice(7)
      if (temp[2].length > 0) {
        temp[1] += ' '
        temp[0] += ' '
      } else if (temp[1].length > 0) {
        temp[0] += ' '
      }
      if (this.mobileAccount.length === 13) {
        this.messageMMType = true
        this.verification()
      } else {
        this.messageMMType = false
      }
      this.mobileAccount = temp.join('')
    },
    /**
   * @description  点击图标密码显隐
   */
    toggle() {
      this.passwordType = !this.passwordType
    },
    /**
   * @description  获取图片验证码
   */
    getPhoto() {
      this.src = this.$HOST + '/v3/platform/web/member/register/getImgCaptcha.html?rnd=' + Math.random() + '&flag=' + localStorage.getItem('sessionValue')
    },
    // 找回密码验证码
    find() {
      const mobileAccountParameter = this.mobileAccount.replace(/\s/g, '')
      if (this.allow) {
        // 手机号验证
        if (!(this.phoneReg.test(mobileAccountParameter))) {
          this.$toast('手机号格式错误')
          return
        }
        // 图片验证校验
        if (this.captchaShow && this.captcha === '') {
          this.$toast('请输入图片验证码')
          return
        }
        let data = {
          mobileNum: mobileAccountParameter,
        }
        if (this.captchaShow) {
          data.captcha = this.captcha
        } else {
          data.v = 1
        }
        personalApi.retrieve(data).then(res => {
          if (res.data.success === false) {
            this.$toast(res.data.message)
          } else {
            gio('track', 'FpSendSmsBtn')
            this.$toast('短信验证码发送成功，请及时验证！')
            // 倒计时
            let time = 60;
            let timer = setInterval(() => {
              if (time === 0) {
                this.allow = true
                clearInterval(timer);
                this.title = '重发验证码';
              } else {
                this.title = '重新发送(' + time + 's)';
                this.allow = false;
                time--
              }
            }, 1000)
          }
        })
      }
    },
    // 修改密码
    changeMm() {
      const mobileAccountParameter = this.mobileAccount.replace(/\s/g, '')
      this.messageMM = this.messageMM.replace(/\s/g, '')
      // 手机号验证
      if (this.mobileAccount === '') {
        this.$toast('请填写手机号')
      }
      if (!(this.phoneReg.test(mobileAccountParameter))) {
        this.$toast('手机号格式错误')
        return
      }
      // 图片验证校验
      if (this.captchaShow && this.captcha === '') {
        this.$toast('请输入图片验证码')
        return
      }
      if (!(this.reg.test(this.password))) {
        this.$toast('密码为数字、字母(区分大小写)、特殊符号中的两种组合，长度为6位-20位')
        return
      }
      let data = {
        captcha: this.messageMM,
        mobileNum: mobileAccountParameter,
        password: this.password,
      }
      if (this.captchaShow) {
        data.imgCaptcha = this.captcha
      } else {
        data.v = 1
      }
      personalApi.changePassword(data).then(res => {
        if (res.data.success) {
          gio('track', 'FpSubmitBtn')
          this.$toast('找回密码成功!')
          this.$router.push('/login')
        } else {
          this.$toast(res.data.message)
        }
      })
    },
    /**
   * @description  账号清除
   */
    clear() {
      this.mobileAccount = ''
    },
    /**
   * @description  图片验证
   */
    verification() {
      gio('track', 'FpMobileInput')
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      if (!(this.phoneReg.test(dealZh))) {
        this.$toast('手机号格式不正确，请重新输入')
        return
      }
      let data = {
        mobile: dealZh
      }
      personalApi.needImg(data).then(res => {
        if (res.data.data) {
          this.captchaShow = true
          this.getPhoto()
        } else {
          this.captchaShow = false
        }
      })
    },
    /**
   * @description  短信验证码输入次数埋点
   */
    safetyCount() {
      gio('track', 'FpSmsInput')
    },
    /**
   * @description  图形验证码输入次数埋点
   */
    verifyCount() {
      gio('track', 'FpPicInput')
    },
    /**
   * @description  新密码输入次数埋点
   */
    detection() {
      gio('track', 'FpNpInput')
    }
  },
}
</script>
